.featured {
  height: 100vh;
}
.featured h1 {
  margin: 0;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 5%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 5%, transparent 90%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
}
.featured--infos {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}
.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-right: 15px;
}
.featured--points {
  color: #46d369;
}
.featured--sinopse {
  margin-top: 15px;
  color: #999;
  font-size: 20px;
  width: 40%;
}
.featured--buttons {
  margin-top: 15px;
}
.featured--List--button,
.featured--watch--button {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  padding: 12px 25px;
  margin-right: 10px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  border: 0px;
  opacity: 1;
  transition: ease all 0.6s;
}
.featured--List--button {
  background: #333;
  color: #fff;
}
.featured--watch--button {
  color: #333;
  background: #fff;
}
.featured--watch--button:hover,
.featured--List--button:hover {
  opacity: 0.7;
}
.featured--genres {
  font-size: 18px;
  margin-top: 15px;
  color: #999;
}
